.profile__job {
    padding: 0;
    margin: 0;
    font-size: 18px;
    line-height: 1.2;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width:767px) {
    .profile__job {
        font-size: 14px;
        text-align: center;
    }
}