.card__caption {
    color: #000;
    box-sizing: border-box;
    max-width: 100%;
    font-size: 24px;
    line-height: 1.1;
    font-weight: 900;
    padding: 25px 21px 31px;
    margin: 0;
    justify-self: start;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}